@import url('https://fonts.googleapis.com/css2?family=Gothic+A1&family=Montserrat:wght@300;600&display=swap');

* {
  margin: 0;
  padding: 0;
}

body {
  /* background-color: #3ae6ca; */
  background-color: #333;
  color: #fff;
  /* font-family: 'Gothic A1', sans-serif; */
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
}

.header {
  background-color: #222;
  padding: 25px 100px;
  font-weight: 600;
}

main {
  margin: 50px 100px;
  width: 50%;
  float: left;
}

aside {
  width: 20%;
  margin-top: 50px;
  margin-left: 5%;
  float: left;
}

main .user {
  /* color: #670a1e; */
  color: #222;
  margin-bottom: 50px;
  /* background-color: #3ae6ca; */
  background-color: #e6ac0c;

  padding: 20px;
}

main .delete-icon {
  float: right;
  color: #134b57;
  cursor: pointer;
  transform: scale(2);
}

main .edit-icon {
  float: right;
  margin-right: 25px;
  color: #165664;
  cursor: pointer;
  transform: scale(2);
}

main .delete-icon:hover,
main .edit-icon:hover {
  opacity: 0.5;
}

form input:not([type='checkbox']),
form textarea,
form button {
  width: 90%;
  padding: 15px 5%;
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
  margin-bottom: 10px;
  border: 0;
  background: #222;
  color: #fff;
  outline: none;
  font-size: 15px;
}

form input[type='checkbox'] {
  margin: 10px;
}

form button {
  width: 100%;
  cursor: pointer;
  transition: transform 500ms ease;
}

form button:hover {
  transform: scale(1.1);
}

.user form {
  margin-top: 20px;
}

.user form input,
.user form textarea,
.user form button {
  background: #2d2828;
}

.user form button:hover {
  transform: scale(1.01);
}
